.opinion{
  margin-top: 36px;
  position: relative;


  @media (min-width: @tablet-width) {
    width: @tablet-width;

  }
  @media (min-width: @desktop-width) {

    width: @desktop-width;

  }


  &::after {
    position: absolute;
    content: "";
    background-image: url(../img/photo-divider.svg);
    height: 1px;
    width: 320px;
    bottom: -25px;
    left: 0;

    @media (min-width: @tablet-width) {
      display: none;
    }


  }
}

.opinion__title{

  .bigText();

  text-align: center;
  text-transform: uppercase;
}

.opinion__text{

  .smallText();

  text-align: center;
  text-transform: uppercase;

  margin-top: 24px;

  @media (min-width: @tablet-width) {
    margin-bottom: 108px;
  }
  @media (min-width: @desktop-width) {
    margin-bottom: 68px;
  }
}

.opinion__br-tablet{
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }
}
